.App {
    position: relative;
    height: 30em;
    width: 50em;
    background: #fff;
    box-shadow: 0px 0px 10px #000;
    overflow: hidden;
    border-radius: 15px;
    padding-bottom: 50px;

    .searchbar {
        position: relative;
        width: 100%;
        font-size: 20px;
        border: none;
        border-bottom: 1px solid #000;
        outline: none;
        padding: 5px 0px;
        padding-left: 25px;
    }

    .students {
        position: relative;
        width: 100%;
        height: 100%;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgba(153, 205, 50, 0.541);
        }
    }
}
