.studentCard {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #000;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 25px;

    img {
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 1px solid #000;
        margin-right: 25px;
    }

    .details {
        position: relative;
        flex-grow: 1;

        .grades {
            margin-top: 25px;
            display: none;
        }

        .tags {
            position: relative;
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: auto;
            margin: 5px 0;

            .tag {
                position: relative;
                display: inline-block;
                background-color: rgb(190, 190, 190);
                padding: 3px;
                border-radius: 2px;
                margin-right: 3px;
            }
        }

        .addTag {
            position: relative;
            border: none;
            border-bottom: 1px solid #000;
            outline: none;
            display: none;
        }
    }

    .btn {
        position: relative;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        cursor: pointer;
        padding: 5px;
        outline: none;
        background: #fff;

        &:hover {
            background: rgb(221, 221, 221);
        }

        svg {
            position: relative;
            width: 100%;
            height: 100%;

            .line1 {
                transition: transform 0.3s ease;
            }
        }
    }
}

.studentCard-active {
    .details {
        position: relative;
        flex-grow: 1;

        .grades {
            display: block;
        }

        .addTag {
            display: block;
        }
    }

    .btn {
        background: rgb(221, 221, 221);

        svg {
            position: relative;
            width: 100%;
            height: 100%;

            .line1 {
                transform: rotate(90deg);
                transform-origin: center;
            }
        }
    }
}
